@import url(https://fonts.googleapis.com/css2?family=Dongle&display=swap);
html {
  box-sizing: border-box;
  font-size: 16px;
  height: 100vh;
  width: 100vw;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  width: 100%;
}

body, h1, h2, h3, h4, h5, h6, p, ol, ul {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

ol, ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

.login-container {
  align-items: center;
  box-shadow: rgba(0, 0, 0, 0.35) 0 5px 15px;
  display: flex;
  flex-direction: column;
  height: 300px;
  justify-content: center;
  margin: 120px auto;
  padding: 0 20px;
  width: 50%;
}

.login-container h1 {
  font-family: monospace;
  font-size: 30px;
  font-weight: 600;
  margin: 0 auto 20px;
  text-align: center;
}

.login-container form {
  display: flex;
  flex-direction: column;
  min-width: 40%;
}

.login-container form input {
  margin-bottom: 7px;
  padding: 7px 0;
  width: 100%;
}

.login-container form button {
  background-color: blueviolet;
  border: none;
  color: #fff0ff;
  cursor: pointer;
  font-weight: 600;
  padding: 9px 0;
}

.login-container form button:hover {
  background-color: rgb(112, 36, 182);
  transition: all;
}

.login-container form button:disabled {
  background-color: gray;
}

@media screen and ( max-width : 450px ) {

  .login-container h1 {
    font-size: 19px;
  }
}

.table-expense-container {
  background-color: #0a0a0a;
  color: #fffffe;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.table-tr {
  align-items: center;
  display: flex;
  justify-content: space-around;
  width: 100%;
}

.table-trHeader {
  padding: 7px 0;
}

.table-trHeader th {
  font-size: 9px;
  width: 10%;
}

.table-body {
  background-color: #141414;
  display: flex;
  justify-content: space-between;
  text-align: right;
}

.table-body:hover {
  background-color: #0a0a0a;
}

.table-body td {
  font-size: 8px;
  text-align: left;
}

.table-body td:nth-child( 1 ) {
  padding-left: 4px;
}

.table-body td:nth-child( 5 ) {
  word-break: break-all;
}

.btn-delete {
  background-color: red;
  border: none;
  color: #fffffe;
  cursor: pointer;
  margin-left: 4px;
  padding: 3px 2px;
}

.btn-delete:hover {
  background-color: #b60202;
}

.btn-edit {
  background-color: #7f5af0;
  border: none;
  color: #fffffe;
  cursor: pointer;
  padding: 3px;
}

.btn-edit:hover {
  background-color: #6f44ee;
}

@media screen and ( min-width : 480px ) {

  .table-trHeader th {
    font-size: 12px;
  }

  .table-body td {
    font-size: 10px;
  }

  .table-body td:nth-child( 5 ) {
    width: 10%;
    word-break: break-all;
  }

}

@media screen and ( min-width : 768px ) {

  .table-trHeader th {
    font-size: 18px;
  }

  .table-body td {
    font-size: 15px;
  }

}


.header {
  align-items: center;
  display: flex;
  height: 70px;
  width: 100%;
}

.header .wallet-svg {
  align-items: center;
  display: flex;
  justify-content: center;
  width: 35%;
}

.header .wallet-svg span {
  font-family: 'Dongle', sans-serif;
  font-size: 15px;
  margin-right: 2px;
}

.header .wallet-svg svg {
  font-size: 30px;
}

.header .user {
  width: 65%;
}

.header .user , .user-info {
  align-items: center;
  display: flex;
  padding-right: 35px;
}

.user-info {
  border: 1px solid rgb(0 0 0 / 24%);
  border-radius: 160px;
  margin-right: 40px;
  padding: 9px 6px;
}

.header .user .user-svg {
  font-size: 19px;
}

.form-container {
  background-color: #004643;
  color: #fffffe;
  display: flex;
  flex-direction: column;
  padding: 19px 10px;
  width: 100%;
}

.form-container label {
  margin-bottom: 10px;
}

select option {
  background-color: #201d1d;
  color: #f0f7f4;
}

.form-container div {
  display: flex;
  justify-content: center;
  width: 100%;
}

.form-container button {
  align-self: center;
  background-color: #f9bc60;
  border: none;
  border-radius: 5px;
  color: #001e1d;
  cursor: pointer;
  display: flex;
  justify-content: center;
  margin-top: 10px;
  padding: 8px 0;
  transition: 0.3s;
  width: 40%;
}

.form-container button:hover {
  background-color: #f8ac3a;
}

@media screen and ( min-width : 480px ) {

  .header {
    height: 90px;
    justify-content: center;
    margin: 0 auto;
  }
  
  .header .wallet-svg span {
    font-size: 12px;
  }

}

@media screen and ( min-width : 768px ) {

  .header {
    padding: 0 35px;
  }

  .header .wallet-svg {
    width: 20%;
  }

  .header .user {
    display: flex;
    justify-content: flex-end;
    width: 75%;
  }

  .header .wallet-svg span {
    font-size: 42px;
  }

  .header .wallet-svg svg {
    font-size: 50px;
    margin-left: 35px;
  }

  .form-container {
    align-items: center;
    flex-flow: row wrap;
  }

  .form-container label {
    margin-bottom: 0;
    margin-left: 5px;
  }

  .form-container label input , .form-container label select {
    padding: 10px;
  }

  .form-container label:nth-child( 1 ) input {
    width: 95px;
  }

  .form-container label:nth-child( 5 ) {
    width: 310px;
  }

  .form-container button {
    border-radius: 10px;
    justify-content: center;
    padding: 14px 0;
    width: 200px;
  }
}

